/* 
Primary Color: Navy Blue (#003366)
Accent Color: Light Gray (#F5F5F5)
Highlight Color: Gold (#FFD700)
Secondary Accent: Dark Gray (#424242)
Complementary Color: White (#FFFFFF)
 */

 /* .App {
    display: flex;
   
  } */
  
  .home {
    /* padding: 1% 1%; */
    margin-top:15%;
    width: 100%;
    height: 100vh;
    background: linear-gradient(303deg, #f5f5f5, #bcc6ce, #f5f5f5, #bcc6ce, #f5f5f5);
  
    background-size: 1000% 1000%;
  
    -webkit-animation: greygradi 30s ease infinite;
    -moz-animation: greygradi 30s ease infinite;
    animation: greygradi 30s ease infinite;  
    align-content: center;
    text-align: center;
    justify-content: center;
  }

  .home h1{
    font-size: 4em;
    color:#003366;
  }
  
  .home button, .game button {
    margin: 2%;
    padding: clamp(10px,1%,50px) clamp(20px,2%,60px);
    font-size: 1em;
    background: #FFD700;
    color:#f5f5f5;
    border:none;
    border-radius: 10%;
    cursor: pointer;
    transition: box-shadow color 0.3s;
  }
  .home button:hover {
    color:#003366;
    box-shadow: 2px 2px 4px #003366;
  }
  
  .game {
    margin-top: 15%;
    padding: 5% 1%;
    width: 100%;
    text-align: center;
    /* align-content: center;
    justify-content: center; */
    background: linear-gradient(303deg, #f5f5f5, #bcc6ce, #f5f5f5, #bcc6ce, #f5f5f5);
  
    background-size: 1000% 1000%;
  
    -webkit-animation: greygradi 30s ease infinite;
    -moz-animation: greygradi 30s ease infinite;
    animation: greygradi 30s ease infinite;
  }
  
  @media (max-width: 485px) {
    /* hero */
    /* .home{
      
      margin-top:30%;
    } */
    .game{
      padding-top:25%;
    }
  }
  
  .board {
    display: grid;
    grid-template-columns: repeat(3, 100px);
    grid-gap: 0px;
    justify-content: center;
    margin: 4%;
  }
  
  .square {
    width: 100px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    border: none;
    cursor: pointer;
    /* background-color: black; */
    background: none;
  
  
  }
  
  .square:nth-child(1),
  .square:nth-child(2),
  .square:nth-child(3) {
    border-bottom: 2px solid black;
  }
  
  .square:nth-child(4),
  .square:nth-child(5),
  .square:nth-child(6) {
    border-top: 2px solid black;
    border-bottom: 2px solid black;
  }
  
  .square:nth-child(7),
  .square:nth-child(8),
  .square:nth-child(9) {
    border-top: 2px solid black;
  }
  
  .square:nth-child(1),
  .square:nth-child(4),
  .square:nth-child(7) {
    border-right: 4px solid black;
  }
  
  .square:nth-child(2),
  .square:nth-child(5),
  .square:nth-child(8) {
    border-left: 2px solid black;
    border-right: 2px solid black;
  }
  
  .square:nth-child(3),
  .square:nth-child(6),
  .square:nth-child(9) {
    border-left: 2px solid black;
  }
  .square.X {
    color: #003366;
  }
  
  .square.O {
    color: #FFD700;
  }
  
  /* button {
    margin: 20px;
    padding: 10px 20px;
    font-size: 16px;
  } */
  
  .hidden {
    display: none;
  }
  
  
  @-webkit-keyframes greygradi {
    0%{background-position:27% 0%}
    50%{background-position:74% 100%}
    100%{background-position:27% 0%}
  }
  @-moz-keyframes greygradi {
    0%{background-position:27% 0%}
    50%{background-position:74% 100%}
    100%{background-position:27% 0%}
  }
  @keyframes greygradi {
    0%{background-position:27% 0%}
    50%{background-position:74% 100%}
    100%{background-position:27% 0%}
  }