/* Weather.css */

.page5 {
    /* margin-top: -6%; */
    margin-top: 9%;
    
    padding-top:5%;
    text-align: center;
    background: linear-gradient(303deg, #f5f5f5, #bcc6ce, #f5f5f5, #bcc6ce, #f5f5f5);
    background-size: 1000% 1000%;
    -webkit-animation: greygradi 30s ease infinite;
    -moz-animation: greygradi 30s ease infinite;
    animation: greygradi 30s ease infinite;
  }
  
  .page5 h1 {
    font-size: 3.5em;
    color: #003366;
    display: flex;
    justify-content: center;
    align-items: center;
    
  }
  /* .icons{
    color:#424242;
  } */
   .icons{
    /* width:fit-content; */
    text-align: center;
   margin-top: 3%;
   color: #424242;
   /* font-size: 4em; */
   }
  
  
  .containerr {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100vh;
    /* margin-left: 10%; */
    margin-top:-2%;
    padding-top: 3%;
  }
  
  @-webkit-keyframes greygradi {
    0% { background-position: 27% 0%; }
    50% { background-position: 74% 100%; }
    100% { background-position: 27% 0%; }
  }
  
  @-moz-keyframes greygradi {
    0% { background-position: 27% 0%; }
    50% { background-position: 74% 100%; }
    100% { background-position: 27% 0%; }
  }
  
  @keyframes greygradi {
    0% { background-position: 27% 0%; }
    50% { background-position: 74% 100%; }
    100% { background-position: 27% 0%; }
  }
  
  .containerr form {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  
  }
  
  .containerr form input {
    font-size: 1.5em;
    border: none;
    width: 55%;
    margin-right: 20px;
    /* margin-left: 1%; */
    outline: none;
    color: black;
    border-bottom: 1px solid #424242;
    background: transparent;
  }
  
  .containerr form input:focus {
    border: none;
  }
  .containerr form button{
    padding: 2% 4%;
  }
  .containerr .current {
    margin-top: 4%;
    /* margin-left: 20%; */
    padding: 8px;
  }
  
  button {
    font-size: 1.5em;
    padding: 1%;
    border: none;
    color: #424242;
    border-radius: 8px;
    background: #F5F5F5;
  }
  
  button:hover {
    box-shadow: 1px 1px 4px #FFD700;
    color: black;
  }
  .loading,.error{
    margin-top:5%;
    font-size: 1.4em;
    color:#424242;
  }
  
  .info {
    display: flex;
    width: 100%;
    margin-left: 2%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 8%;
    font-size: 1.3em;
  }
  .info p{
    margin-top:0px;
  } 
  
  @media (max-width:410px) {
    .page5{
        margin-top: 21%;
    }
    .page5 h1 {
  font-size: 2.8em;
  
    }
    button{
      font-size: 1.2em;
    }
    .info{
      font-size: 1.1em;
    }
  }